'use strict';

define('modules/addTitle', ['jquery'], function (require) {
    'use strict';

    console.log('modules/addTitle');

    var util = {
        title: function title(cls, txt) {
            $('body').prepend('<h2 class="' + cls + '">' + txt + '</h2>');
        },
        copyRight: function copyRight(txt) {
            $('body').append('<p class="copyright">' + txt + '</p>');
        },
        append: function append(txt) {
            $('body').append('<span>' + txt + '</span>');
        }
    };
    return util;
});